//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
	//import引入的组件需要注入到对象中才能使用
	components: {},
	data() {
		//这里存放数据
		return {
			cateId: '',
			goodId: '',
			indexs: 0,
			subList: [],
			goodsList: []
		};
	},
	//监听属性 类似于data概念
	computed: {},
	//监控data中的数据变化
	watch: {},
	//方法集合
	methods: {
		iconBack() {
			uni.navigateBack()
		},
		tabItem(item, index) {
			this.goodId = item.id
			this.indexs = index
			console.log(item)
			uni.request({
				url: this.$api(`goods/goodsList?categoryId=${this.goodId}`),
				method: 'GET',
				data: {},
				success: res => {
					uni.showLoading({
						title: '加载中',
					});
					this.goodsList = res.data.data
					console.log(this.goodsList)
					uni.hideLoading({
						title: "加载成功"
					})
				},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	//生命周期 - 创建完成（可以访问当前this实例）
	created() {

	},
	onLoad(option) {
		console.log(option)
		this.cateId = option.cateId
		this.indexs = option.indexs
		this.goodId = option.goodId
	},
	//生命周期 - 挂载完成（可以访问DOM元素）
	mounted() {
		console.log(this.cateId)
		uni.request({
			url: this.$api(`category/currentaction?id=${this.cateId}`),
			method: 'GET',
			data: {},
			success: res => {
				uni.showLoading({
					title: '加载中',
				});
				// console.log(res)
				this.subList = res.data.data.currentOne.subList
				// console.log(this.subList)
			},
			fail: () => {},
			complete: () => {}
		});
		uni.request({
			url: this.$api(`goods/goodsList?categoryId=${this.goodId}`),
			method: 'GET',
			data: {},
			success: res => {
				uni.showLoading({
					title: '加载中',
				});
				console.log(res)
				this.goodsList = res.data.data
				console.log(this.goodsList)
				uni.hideLoading({
					title: "加载成功"
				})
			},
			fail: () => {
				uni.hideLoading({
					title: "加载失败"
				})
			},
			complete: () => {}
		});
	},
	beforeCreate() {}, //生命周期 - 创建之前
	beforeMount() {}, //生命周期 - 挂载之前
	beforeUpdate() {}, //生命周期 - 更新之前
	updated() {}, //生命周期 - 更新之后
	beforeDestroy() {}, //生命周期 - 销毁之前
	destroyed() {}, //生命周期 - 销毁完成
	activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
